<template>
	<div class="header">
    <div class="background">
        <img :src="seller.avatar" alt="">
    </div>
    <div class="content-wrapper">
        <img :src="seller.avatar" alt="" class="avatar">
        <div class="content">
            <div class="title">
                <img src="../../../static/imgs/brand.png" alt="" class="brand">
                <span class="name">{{seller.name}}</span>
            </div>
            <div class="description">{{seller.description+"/"+seller.deliveryTime+"分钟送达"}}</div>
            <div class="cut">
                <img src="../../../static/imgs/decrease.png" alt="" class="icon">
                <span class="text" v-if="seller.supports">{{seller.supports[0].description}}</span>
            </div>
        </div>
       
    </div>
         <div class="support-count" @click="show">
            <span class="count" v-if="seller.supports">{{seller.supports.length+"个"}}</span>
            <i class="el-icon-arrow-right"></i>
        </div>
        <div class="bulletin-wrapper" @click="show">
            <img src="../../../static/imgs/bulletin.png" alt="" class="bulletin-title">
            <span class="bulletin-text">{{seller.bulletin}}</span>
            <i class="el-icon-arrow-right"></i>
        </div>
        <Detail></Detail>
      
    </div>
</template>
<script>
	import {mapGetters} from 'vuex'
    import Detail from '../detail/detail.vue'
    export default {
        computed:{
            ...mapGetters(['seller'])
        },
        components:{
            Detail
        },
        created(){
            this.$store.dispatch('getSeller');
        },
        methods:{
            show(){
                this.$store.dispatch('showdetail');
               
            }
        }
    }
</script>
<style lang="less" scoped>
	.header{
        height:2.68rem;
        background:rgba(7,17,27,0.5);
        position:relative;

    }
    .background{
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            z-index: -5;
            filter:blur(5px);   
            img{
                width: 100%;
                height: 100%;
            }            
        }

    
    .content-wrapper{
        position:absolute;
        top:0.48rem;
        left:0.48rem;
    }
    .avatar{
        
        width:1.28rem;
        height:1.28rem;
        border-radius:0.04rem;
        position:absolute;

    }
    .title{
        margin-bottom:0.16rem;

    }
    .content{
        position:relative;
        margin-left:1.6rem;

    }
    .brand{
        vertical-align:top;
        height:0.36rem;
    }
    .name{
        margin-left:0.12rem;
        font-size:0.32rem;
        color:rgb(255,255,255);
        font-weight:bold;
        line-height:0.36rem;
        vertical-align:top;
      
    }
    .description{
        font-size:0.24rem;
        color:rgb(255,255,255);
        font-weight:200;
        line-height:0.24rem;
        margin-bottom:0.2rem;
    }
    .icon{
        vertical-align:middle;
        height:0.25rem;
        width:0.25rem;
    }
    .text{
        font-size:0.2rem;
        color:#fff;
        font-weight:200;
        line-height:0.25rem;
        vertical-align:middle;
    }
    .support-count{
        position:absolute;
        top:1.3rem;
        right:0.24rem;
        height:0.48rem;
        color:#fff;
        width:1rem;
        line-height:0.48rem;
        border-radius:14px;
        background-color:rgba(0,0,0,0.2);
        text-align:center;
        font-size:0.2rem;
    }
   
    .count{
        
        font-weight:200;
       
    }
    .bulletin-wrapper{
        position:absolute;
        color:#fff;
        bottom:0;
        left:0;
        height:0.56rem;
        line-height:0.56rem;
        width:100%;
        background:rgba(7,17,27,0.2);
    }
    .bulletin-title{
        padding-left:0.24rem;
        height:0.3rem;
        vertical-align:middle;

    }
    
    .bulletin-text{
        display:inline-block;
        padding-left:0.08rem;
        font-size:0.2rem;
        white-space:nowrap;
        overflow:hidden;
        text-overflow:ellipsis;
        width:82%;
        font-weight:200;
        vertical-align:middle;

            
    }

</style>


