<template>
 	
	<div class="cartControl">
		<transition name='move'>
			<div class="reduce"@click.stop="decrement" v-show="food.num">
			 	<i class="icon iconfont icon-jianhao"></i>
			</div>
		</transition>		
		<div class="count" v-show="food.num">{{food.num}}</div>
		<div class="add" @click.stop="increment">
			<i class="icon iconfont icon-jiahao"></i>
		</div>
	</div>
	
</template>
<script>
	import Vue from 'vue'
	import {mapGetters} from 'vuex'

	export default{
		props:['food'],
		methods:{
			increment(e){
				 if( !e._constructed){
                    return;
                };
				if(!this.food.num){
					Vue.set(this.food,"num",1);
				}else{
					this.food.num++;
				}
				
				
			},
			decrement(e){
				 if( !e._constructed){
                    return;
                };
					if(this.food.num){
						this.food.num--;
					}
				}
		}
	}
</script>
<style lang="less">
	.cartControl{
		.move-enter-active, .move-leave-active {
            transition: all 0.5s;
        }
        .move-leave-active {
            transition: all 0.5s;
            transform:translate(30px) rotate(90deg);
        }
        // count>0时候的动画，v-show显示时的动画效果
        .move-enter, .move-leave-active {
            transform:translate(20px) rotate(90deg);
            opacity: 0
        }
		.add{
			display:inline-block;
			width:0.36rem;
			line-height: 0.36rem;
			vertical-align:middle;
			i{
				font-size:0.36rem;
				line-height:0.36rem;
				color:#00a0dc;

			}
		}
		.count{
			padding:0 0.12rem;
			font-size: 0.2rem;
			display: inline-block;
			vertical-align:middle;
			text-align: center;
			color: gray;
			width: 0.24rem;
            line-height: 0.36rem;
		}
		.reduce{
			display:inline-block;
			width:0.36rem;
			vertical-align:middle;
			line-height: 0.36rem;

			i{
				font-size:0.36rem;
				line-height:0.36rem;
				color:#00a0dc;
			}
		}
	}
</style>


