import types from '../types.js'
import axios from 'axios'
const state = {
	ratings:[],
	types:["满意","不满意","全部"],
	selectType:2,
	onlyContent:false,
	selectRatings:[]
	

}
const getters = {
	ratings(state){
		return state.ratings;
	},
	types(state){
		return state.types;
	},
	selectType(state){
		return state.selectType;
	},
	onlyContent(state){
		return state.onlyContent;
	},
	selectRatings(state){
		return state.selectRatings;
	}
}
const actions = {
	getRatings({commit,state}){
		axios.get('../../../static/data.json').then(resp=>{
			if(resp.data.errno=== 0 ){
				commit(types.GET_RATINGS,resp.data.ratings)
			}
		})
	},
	resetType({commit},Types){
		commit(types.RESET_TYPES,Types);
	},
	select({commit},type){
		commit(types.SELECT,type);
	},
	toggle({commit}){
		commit(types.TOGGLE);
	}
}
const mutations = {
	[types.GET_RATINGS](state,data){
		state.ratings = data;
		
	},

	[types.RESET_TYPES](state,Types){
		state.types = Types;
		state.selectType = 2;
		state.selectRatings = state.ratings;

	},
	[types.SELECT](state,type){
		state.selectType = type;
		state.selectRatings = state.ratings.filter( rating => {
			if( state.onlyContent && !rating.text ){
				return false;
			}else if( state.selectType === 2 ){
				return true;
			}else{
				return state.selectType === rating.rateType;
			}
		})
	},
	[types.TOGGLE](state){
		state.onlyContent = !state.onlyContent;

		state.selectRatings = state.ratings.filter( rating => {
			if( state.onlyContent && !rating.text ){
				return false;
			}else if( state.selectType === 2 ){
				return true;
			}else{
				return state.selectType === rating.rateType;
			}
		})
	}

}

export default {
    state,
    getters,
    actions,
    mutations
}


