<template>
	<div class="ratings" ref="ratingsWrap">
		<div>
			<div class="info">
				<div class="left-info">
					<div class="num">{{seller.score}}</div>
					<div class="text">综合评分</div>
					<div class="score">高于周边商家{{seller.rankRate}}%</div>
				</div>
				<div class="right-info">
					<div class="serviceScore">
						<span class="text">服务态度</span>
						<star class="star" :size="36" :score="seller.serviceScore"></star>
						<span class="score">{{seller.serviceScore }}</span>
					</div>
					<div class="foodScore">
						<span class="text">商品评分</span>
						<star class="star" :size="36" :score="seller.foodScore"></star>
						<span class="score">{{seller.foodScore}}</span>
					</div>
					<div class="times">
						<span class="text">送达时间</span>
						<span class="min">{{seller.deliveryTime}}分钟</span>
					</div>
					
				</div>
			</div>
			<div class="midline"></div>
			
			<ratingselect></ratingselect>
			
			<!-- 评价列表 -->
			<div class="rating-wrap">
				<ul>
					<li v-for="rating in selectRatings" class="rating-item">
					<!-- 头像 -->
						<div class="avatar">
							<img :src="rating.avatar" alt="">
						</div>
						<!-- 内容 -->
						<div class="content">
							<h1 class="name">{{rating.username}}</h1>
							<div class="star">
								<star :size="24" :score="rating.score"></star>
								<span class="deliveryTime">{{rating.deliveryTime}}分钟送达</span>
							</div>
							<p class="text">{{rating.text}}</p>
							<div class="recommend">
								<span>
									<i v-show="rating.rateType ===0" class="icon iconfont icon-damuzhion"></i>
									<i v-show = 'rating.rateType === 1' class="icon iconfont icon-down"></i>
								</span>
								<span class="item" v-for="item in rating.recommend">{{item}}</span>
							</div>
							<div class="time">{{rating.rateTime | getTimes}}</div>
						</div>
					</li>
				</ul>
			</div>
		</div>
			


		
	</div>
</template>
<script>
	import {mapGetters} from 'vuex'
	import star from '../star/star'
	import ratingselect from '../ratingSelect/ratingSelect.vue'

	export default{

		
		computed:{
			...mapGetters([
				'selectRatings',
				'seller'
			])
		},
		created(){
			this.$store.dispatch('getRatings');
			this.$store.dispatch('getSeller');
			setTimeout(()=> {
				this.$store.dispatch('resetType',['满意','不满意','全部']);
				this.$nextTick(() => {
					this.scroll=new BScroll(this.$refs.ratingsWrap,{
					 	click:true
					});
				});
			},100) 
		},

		filters:{
			getTimes(date){
				var time = new Date(date);
				var y = time.getFullYear();
				var m = time.getMonth() + 1;
				var d = time.getDay();
				var h = time.getHours();
				var min = time.getMinutes();
				var s = time.getSeconds();
				return (y+'-'+m+'-'+d+'-'+h+'-'+min+'-'+s);

			}
		},

		watch:{
			selectRatings(){
				this.$nextTick(() => {
					if( !this.scroll ){
						this.scroll=new BScroll(this.$refs.ratingsWrap,{
							click:true
						});
					}else{
						this.scroll.refresh();
					}
				})
			}
		},
		// methods:{
		// 	_initratings(){
		// 		this.ratingswrap = new BScroll(this.$refs.ratingsWrap,{
		// 			click:true
		// 		});
		// 	}

			
		// },
		components:{
			star,
			ratingselect
		}
	}
</script>
<style lang="less">
	.ratings{
        position: absolute;
        top: 3.48rem;
        bottom: 0;
        left: 0;
        width: 100%;
        overflow:hidden;
        .info{
        	display:flex;
        	border-bottom:1px solid rgba(7,17,27,0.1);
			.left-info{
				padding:0.36rem 0;
				flex:0 0 2.75rem;
				width:2.75rem;
				text-align:center;
				border-right:1px solid rgba(7,17,27,0.1);
				.num{
					font-size:0.48rem;
					color:rgb(255,153,0);
					line-height:0.56rem;
					margin-bottom:0.12rem;
				}
				.text{
					font-size:0.24rem;
					color:rgb(7,17,27);
					line-height:0.24rem;
					font-weight:700;
					margin-bottom:0.16rem;
				}
				.score{
					font-size:0.2rem;
					line-height:0.2rem;
					color:rgb(147,153,159);
				}
			}
			.right-info{
					 padding: 0.36rem 0.16rem 0.36rem 0.36rem;
				.serviceScore,.foodScore{
					line-height:0.36rem;
					// margin-bottom:0.16rem;
					.text{
						font-size:0.24rem;
						color:rgb(7,17,27);
						line-height:0.36rem;
					}
					.star{
						display:inline-block;
						vertical-align:middle;
						margin-left:0.24rem;
					}
					.score{
						vertical-align:middle;
                        font-size:0.24rem;
                        line-height:0.36rem;
                        color:rgb(255,153,0);
                        margin-right:0.48rem;
					}
				}
				.times{
					.text{
						font-size:0.24rem;
						color:rgb(7,17,27);
						line-height:0.36rem;
					}
					.min{
						font-size:0.24rem;
						margin-left:0.24rem;
						color:rgb(147,153,159);
					}
				}
			}
        }
        .midline{
        	width:100%;
        	height:0.32rem;
        	background:#f3f5f7;
        }
        .rating-wrap{
   			
        	.rating-item{
        		display: flex;
        		padding:0.36rem 0.36rem;
        		border-bottom:1px solid rgba(7,17,27,0.1);
        		.avatar{
        			// width:0.56rem;
        			margin-right:0.24rem;
        			img{
        				width:0.56rem;
        				border-radius:50%;
        			}
        		}
        		.content{
        			position:relative;
        			flex:1;
					.name{
						font-size:0.2rem;
						color:rgb(7,17,27);
						line-height:0.24rem;
						font-weight:700;
						margin-bottom:0.08rem;

					}
					.star{
						display:inline-block;
						padding-right:0.12rem;
					}
        			.deliveryTime{
        				display:inline-block;
        				font-size:0.2rem;
        				font-weight:200;
        				color:rgb(147,153,159);
        				margin-bottom:0.12rem;
        			}
        			.text{
        				font-size: 0.24rem;
        				color:rgb(7,17,27);
        				outline-width:0.36rem;
        				font-weight:700;
        				margin-bottom:0.16rem;

        			}
        			.recommend{
        				
        				margin-right:0.16rem;
        				.icon-damuzhion{
        					color:rgb(0,160,220);
        					font-size:0.2rem;
        					}
        				.icon-down{
        					color:rgb(147,153,159);
        					font-size:0.2rem;
        					}
        			}
        			
        			.item{
        				padding:0 0.16rem;
        				margin-right:0.16rem;
        				border:1px solid rgba(7,17,27,0.1);
        				border-width:1px;
        				border-radius:0.02rem;
        				color:rgb(147,153,159);
        				background:#fff;
        				font-size:0.18rem;
        				line-height:0.32rem;



        			}
        			.time{
        				position:absolute;
        				top:0;
        				right:0;
        				font-size:0.2rem;
        				font-weight:200;
        				line-height:0.24rem;
        				color:rgb(147,153,159);

        			}
        		
        		}

        	}
        }
    }    
</style>


