<template>
	<div class="goods">
		<div class="goods-menu" ref="menuWrapper">
			<ul>
				<li v-for="(v,i) in goods" class="menu-list" :class="{'current' : i=== currentIndex}" @click="selectMenu(i,$event)">
				
					<span class="text"><span v-show="v.type>0" class="icon" :class="iconClassMap[v.type]"></span>{{v.name}}</span>
				</li>
			</ul>	
		</div>
		<div class="goods-foods" ref="foodsWrapper">
			<ul>
				<li v-for="item in goods" class="food-list food-list-hook">
					<h1>{{item.name}}</h1>
					<ul>
						<li v-for="food in item.foods" class="foods-item" >
							
								<img :src="food.icon" alt="" @click='showfood(food,$event)'>
							
							<div class="content">
								<h2  @click='showfood(food,$event)'>{{food.name}}</h2>
								<p v-show="food.description" class="description">{{food.description}}</p>
								<div class="sell-info">
									<span class="sellCount">月售{{food.sellCount}}份</span>
									<span class="rating">好评率{{food.rating}}%</span>
								</div>
								<div class="price">
									<span class="sign">￥</span><span class="newSprice">{{food.price}}</span><span v-show="food.oldPrice" class="oldPrice">￥{{food.oldPrice}}</span>
									<cartControl :food="food" class="cart"></cartControl>
								</div>
							</div>
						</li>
					</ul>
				</li>
			</ul>
		</div>
		<foodDetail></foodDetail>
	</div>

</template>
<script>
	import {mapGetters} from 'vuex'
	import foodDetail from '../detail/foodDetail.vue'
	import cartControl from '../cartControl/cartControl.vue'
	export default{
		data(){
			return {
				listHeight:[],
				scrollY:0
			}
		},
		computed:{
			...mapGetters(['goods']),
			currentIndex(){
				for(let i=0;i<this.listHeight.length;i++){
					let height1 = this.listHeight[i];
					let height2 = this.listHeight[i+1];
					if(this.scrollY >= height1 && this.scrollY < height2){
						return i;
					}
				}
				return 0;
			}
		},
		created(){
			this.$store.dispatch('getGoods');
			this.iconClassMap = ['decrease','discount','special','invoice','guarantee'];
			this.$nextTick(()=>{
				// console.log(this.calcHeight);
				setTimeout(()=>{
					this._initmenu();
					this._initfoods();
					this._calcHeight();
					},100);
			});
			
		},
		methods:{
			showfood(f,e){
				this.$store.dispatch('showfood',f);
			},

			selectMenu(key,e){
				let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
				let el = foodList[key];
				this.foodswrap.scrollToElement(el,300)
			},

			_calcHeight(){
				let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');

				let height = 0;
				this.listHeight.push(height);
				for(let i=0;i<foodList.length;i++){
					height+=foodList[i].clientHeight;
					this.listHeight.push(height);
				}
				// console.log(this.listHeight);
			},
			_initmenu(){
				this.menuwrap = new BScroll(this.$refs.menuWrapper,{
					click:true
				});
			},
			_initfoods(){
				this.foodswrap = new BScroll(this.$refs.foodsWrapper,{
					probeType:3,
					click:true
				});
				this.foodswrap.on('scroll',(pos)=>{
					this.scrollY=Math.abs(Math.round(pos.y));
					// console.log(this.scrollY);
				});
			}
		},
		components:{
			foodDetail,
			cartControl

		}
	}

</script>
<style lang="less">
	.goods{
	position:absolute;
	top:3.48rem;
	bottom:0.92rem;
	width:100%;
	display:flex;
	.goods-menu{
		flex:0 0 1.6rem;
		width:1.6rem; 
	
		overflow:hidden;
		margin-top:0.02rem;
		
		.menu-list{
			position:relative;
			padding:0 0.24rem;
			display:table;
			background:#f3f5f7;
			height:1.08rem;
			padding-left:0.24rem;
			line-height:0.24rem;
			&.current{
				background: white;
                z-index: 10;
                
			}
			
			.text{
			text-align:center;
			width:1.12rem;
			border-bottom:0.01rem solid rgba(7,17,27,0.1);
			display:table-cell;
            vertical-align:middle;
			font-size:0.24rem;
			line-height:0.28rem;
			color:black;
			font-weight:200;
			white-space:normal;
			.icon{
				display:inline-block;
				width:0.32rem;
				height:0.32rem;
				
  				background-repeat:no-repeat;
  				vertical-align:middle;

				&.decrease{
					background:url(../../../static/imgs/decrease.png);
					background-size:cover;
					
				}
				&.discount{
					background:url(../../../static/imgs/discount.png);
					background-size:cover;
					
				}
				&.special{
					background:url(../../../static/imgs/special.png);
					background-size:cover;
					
				}
				&.invoice{
					background:url(../../../static/imgs/invoice.png);
					background-size:cover;
					
				}
				&.guarantee{
					background:url(../../../static/imgs/guarantee.png);
					background-size:cover;
					
				}

			}
			
			

			}
		}
		
	}
	.goods-foods{
		flex:1;
		overflow:hidden;
		.food-list{
			h1{
				height:0.52rem;
				padding-left:0.28rem;
				color:rgb(147,153,159);
				font-size:0.24rem;
				line-height:0.52rem;
				background:#f3f5f7;
				border-left:2px solid #d9dde1;

			}
		}
		.foods-item{
			position:relative;
			display:flex;
			padding:0.36rem 0.36rem;
			border-bottom:1px solid rgba(7,17,27,0.1);
			img{
				height:1.14rem;
				width:1.14rem;

			}
			.content{
				flex:1;
				padding-left:0.2rem;
				h2{
					height:0.28rem;
					margin:0.02rem 0 0.08rem 0;
					font-size:0.28rem;
					color:rgb(7,17,27);
					line-height:0.28rem;
					font-weight:700;
					margin-bottom:0.16rem;
					
				}
				.description{
					font-size:0.2rem;
            		margin-bottom:0.16rem;
            		line-height:0.2rem;
            		color:rgb(147,153,159);
				}
				.sell-info{
					font-size:0.2rem;
            		margin-bottom:0.16rem;
            		line-height:0.2rem;
            		color:rgb(147,153,159);
            		.sellCount{
            			margin-right:0.1rem;
            		}
				}
				.price{
					color:rgb(240,20,20);
				 	.sign{
				 		font-size:0.2rem;
				 	}
				 	.newSprice{
				 		font-size:0.28rem;
				 		font-weight:700;
				 	}

            		.cart{
            			position: absolute;
                        right: 0.36rem;
                        bottom:0.2rem;

            		}
				}
           

			}
		}
	}	
	}
</style>


