<template>
	<div class="footer">
		<div class="footer-left">
			<div class="outCart">
				<div class="inCart" :class="{'highlight' : totalCount > 0}" @click="showcart">
					<i class="icon iconfont icon-gouwuche" :class="{'highlight' : totalCount > 0}"></i>
				</div>
				<div class="num"  v-show = 'totalCount > 0'>{{totalCount}}</div>
			</div>
			<div class="price" :class="{'highlight' : totalCount > 0}">
				￥{{totalPrice}}
			</div>
			<div class="desc">另需配送费￥4元</div>
		</div>
		<div class="footer-right">
			<div class="pay" :class='{"enough":totalPrice>=seller.minPrice}'>{{payDesc}}</div>
		</div>
		<transition enter-active-class="animated fadeInUp" leave-active-class="animated fadeOutDown">
			<div class="shopcart-list" v-show="listShow">
	
			<div class="cartheater">
				<h1>购物车</h1>
				<span @click="empty">清空</span>
			</div>
			<div class="cart-list" ref="cartWarp">
		
				<ul>
					<li v-for="food in selectFoods" class="food">
						<span class="name">{{food.name}}</span>
                        <div class="price">￥<span>{{ food.price * food.num }}</span><span><cartControl class="cartControl" :food ='food'></cartControl></span>
                        </div>                       
                        	                       
					</li>
				</ul>		
		
							
			</div>
		</div>		
		</transition>
		<transition name="mask">
			<div class="list-mask" v-show = 'listShow' @click ='showcart'></div>
		</transition>
		
	</div>
</template>
<script>
    import {mapGetters} from 'vuex'
    import cartControl from '../cartControl/cartControl.vue' 

	export default{
		data(){
			return {
				flag:false
			}
		},
		watch:{
			food(){
				
			}
			
		},
		computed:{
			...mapGetters([
					'goods',
					'seller',
					'food'
				]),
			selectFoods(){
				let foods=[];
				this.goods.forEach( good =>{
					good.foods.forEach( food =>{
						if(food.num>0){
							foods.push(food);
						}
					})
				})
				return foods;
			},
			totalPrice(){
				let total = 0;
				this.selectFoods.forEach( food =>{
					total+=food.price*food.num;
				});
				return total;
			},
			totalCount(){
				let count = 0;
				this.selectFoods.forEach( food =>{
					count+=food.num;
				});
				return count
			},
			payDesc(){
				if(this.totalPrice===0){
					return `￥${this.seller.minPrice}元起送`
				}else if(this.totalPrice<this.seller.minPrice){
					return `还差${this.seller.minPrice-this.totalPrice}元起送`
				}else{
					return "去结算"
				}
			},
			listShow(){
				if(!this.totalPrice){
					this.flag=true;
					return false;
				}
				let show = !this.flag;
				return show;
			}

		},
		methods:{
			showcart(){
				if(!this.totalPrice){
					return
				}
				this.flag = !this.flag;
				if(!this.flag){
					setTimeout(()=>{
						if( !this.cartwarp ) {
							this.cartwarp = new BScroll(this.$refs.cartWarp,{
								click:true
							});
						}else{
							this.cartwarp.refresh();
						}
					},1000)
					
				}
			},
			empty(){
				this.selectFoods.forEach( food =>{
					food.num=0;
				});
			}
			
		},

		components:{
			cartControl
		}
	}
</script>
<style lang="less">
	.footer{
		width:100%;
		height:0.96rem;
		background:#141d27;
		position:fixed;
		bottom:0;
		z-index:5;
		display:flex;
		.footer-left{
			flex:1;
			background: #141d27;
			.outCart{
				display:inline-block;
				position:absolute;
				top:-0.2rem;
				width:1.12rem;
				height:1.12rem;
				border-radius: 50%;
				background: #141d27;
				margin-left:0.24rem;
				padding:0.12rem;
				box-sizing: border-box;
				.inCart{
					width: 0.88rem;
					height: 0.88rem;
					line-height: 0.88rem;
					border-radius: 50%;
					text-align: center;
					background-color: #2b343c;
					&.highlight{
                            background:#00a0dc;
                        }
					i{
						font-size: 0.48rem;
						color:rgba(255,255,255,0.4);
						&.highlight{
                                color: white;
                            }
					}
				}
				.num{
					position: absolute;
                    top:0;
                    right: 0;
                    width: 0.48rem;
                    height: 0.32rem;
                    line-height: 0.37rem;
                    text-align: center;
                    border-radius: 0.16rem;
                    font-size: 0.09rem;
                    font-weight: 700;
                    color:white;
                    background: rgb(240,20,20);
                    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
				}

			}
			.price{
				display: inline-block;
                    font-size: 0.32rem;
                    font-weight: 700;
                    line-height: 0.48rem;
                    color:rgba(255,255,255,0.4);
                    margin: 0.24rem 0;
                    margin-left:1.6rem;
                   padding-right: 0.24rem;
                    border-right: 1px solid rgba(255,255,255,0.1);
                    &.highlight{
                        color: #fff;
                    }
			}
			.desc{
                    display: inline-block;
                    font-size: 0.30rem;
                    font-weight: 300;
                    line-height: 0.48rem;
                    color:rgba(255,255,255,0.4);
                    vertical-align: top;
                    margin-top: 0.28rem;
                   
                    padding-left: 0.24rem;
                }           
		}
		.footer-right{
			 flex:0 0 2.05rem;
             width: 2.05rem;
             .pay{
                    font-size: 0.24rem;
                    font-weight: 700;
                    line-height: 0.98rem;
                    height: 0.96rem;
                    color:#26333b;
                    background-color: #2b343c;
                    color:rgba(255,255,255,0.4);
                    text-align: center;
                    &.not-enough{
                        background: #2b333b;
                    }
                    &.enough{
                        background: #00b43c;
                        color: #fff;
                    }
                }
		}
		.shopcart-list{
			position: absolute;
			bottom:1rem;
            left: 0;
           
            z-index: -1;
            width: 100%;
            background: #fff;
            
			.cartheater{
				height:0.8rem;
				line-height:0.8rem;
				display:flex;
				justify-content:space-between;
				padding:0 0.36rem;
				text-align:center;
				border-bottom: 1px solid rgba(7,17,27,0.1);
				h1{
					font-size:0.28rem;
					font-weight: 200;
					color: rgb(7,17,27);
				}
				span{
					font-size: 0.24rem;
	                color:rgb(0,160,220);
				}


			}
		}
		.cart-list{
			padding:0 0.36rem;
			overflow: hidden;
			max-height: 4rem;
			.food{
				padding:0.24rem 0;
				display: flex;
				justify-content:space-between;
				border-bottom:1px solid rgba(7,17,27,0.1);
				
				
				.name{
					font-size: 0.28rem;
					line-height: 0.48rem;
					color:rgb(7,17,27);
				
				}
				.price{
					font-size:0.2rem;
					font-weight: normal;
					color:rgb(240,20,20);
					line-height:0.48rem;
					vertical-align:middle;
					
					span{
						vertical-align:middle;
						font-size: 0.28rem;
						font-weight: 700;
						color:rgb(240,20,20);
						line-height:0.48rem;
						margin-right:0.24rem;
						.cartControl{
						display: inline-block;
						font-size: 0.48rem;
						line-height: 0.48rem;
						

					}
					}
					
				}
			}
			
		}
		.mask-enter-active, .mask-leave-active {
            transition: all 0.8s;
            opacity: 1;
        }
    .mask-enter, .mask-leave-active {
            opacity: 0;
        }
    .list-mask{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -2;
        background:rgba(7,17,27,0.6);
    }

		
	}
</style>


