<template>
	<div class="detail" v-show="detailshow">
		<div>
			<div class="detail-box">
				<h1 class="name">{{seller.name}}</h1>
				<div class="star">
					<star :size="48" :score="seller.score"></star>
				</div>
				<div class="title">
					<div class="line"></div>
					<div class="text">优惠信息</div>
					<div class="line"></div>
				</div>
				<ul v-if="seller.supports" class="supports">
					<li class="supports-item" v-for="item in seller.supports">
						<span class="icon" :class="iconClassMap[item.type]"></span>
						<span class="text">{{item.description}}</span>
					</li>
				</ul>
				<div class="title">
					<div class="line"></div>
					<div class="text">商家公告</div>
					<div class="line"></div>
				</div>
				<div class="bulletin">{{seller.bulletin}}</div>
				<div class="close" @click="close">
					<i class="el-icon-close"></i>
				</div>
			</div>
		</div>
		
	</div>
</template>
<script>
	import {mapGetters} from 'vuex'
	import star from '../star/star'
	export default {
		computed:{
			...mapGetters([
				'detailshow',
				'seller'
				])
		},
		created(){
			this.$store.dispatch('getSeller');
			this.iconClassMap = ['decrease','discount','special','invoice','guarantee']
		},
		components:{
			star
		},
		methods:{
			close(){
				this.$store.dispatch('closedetail')
			}
		}
		
	}
</script>
<style lang="less">
	.detail{
		position:fixed;
		top:0;
		left:0;
		right:0;
		bottom:0;
		background:rgba(7,17,27,0.8);
		z-index:999;
		.detail-box{
			margin-top:1.28rem;
			padding-bottom:0.64rem;
			.name{
				font-size:0.32rem;
				font-weight:700;
				color:#fff;
				line-height:0.32rem;
				text-align:center;
				margin-bottom:0.32rem;
			}
			.star{
				text-align:center;
				margin-bottom:0.56rem;
			}
			.title{
				display:flex;
				width:80%;
				margin:0 0.72rem;
				margin-bottom:0.48rem;
				color:#fff;
				.line{
					position:relative;
					top:0.1rem;
					display:inline-block;
					flex:1;
					height:0.01rem;
					background:rgba(255,255,255,0.2);
					margin:0 auto;
					
				}
				.text{
					padding:0 0.24rem;
					font-size:0.3rem;
					font-weight:700;
					vertical-align:middle;
					
				}

			}
			.supports{
					padding:0 0.96rem;
					margin-bottom:0.56rem;
					.supports-item{
						color:#fff;
						padding:0 0 0.24rem 0;
						.text{
							vertical-align:middle;
							font-size:0.24rem;
							font-weight:200;
							line-height:0.24rem;

						}
						.icon{
							display:inline-block;
							width:0.32rem;
							height:0.32rem;
							
              				background-repeat:no-repeat;
              				vertical-align:middle;

							&.decrease{
								background:url(../../../static/imgs/decrease.png);
								background-size:cover;
								
							}
							&.discount{
								background:url(../../../static/imgs/discount.png);
								background-size:cover;
								
							}
							&.special{
								background:url(../../../static/imgs/special.png);
								background-size:cover;
								
							}
							&.invoice{
								background:url(../../../static/imgs/invoice.png);
								background-size:cover;
								
							}
							&.guarantee{
								background:url(../../../static/imgs/guarantee.png);
								background-size:cover;
								
							}

						}
					}

				}
			.bulletin{
				font-size:0.24rem;
				font-weight:200;
				color:#fff;
				line-height:0.48rem;
				padding:0 0.96rem;
			}
			.close{
				color:rgba(255,255,255,0.5);
				font-size:0.32rem;
				position:absolute;
				bottom:0.64rem;
				left:50%;



			}
		}
	}
</style>


