<template>
	<div class="star" :class="starSize">
		<span v-for="starClass in starClasses" :class="starClass" class="star-item">
			
		</span>
	</div>
</template>
<script>
	const LENGTH = 5
	const STAR_ON = 'on'
	const STAR_HALF = 'half'
	const STAR_OFF = 'off'

	export default{
		props:{
			size:{type:Number},
			score:{type:Number}
		},
		computed:{
			starSize(){
				return 'star-' + this.size;
			},
			starClasses(){
				let result = [];
				let score = Math.floor(this.score*2)/2;
				let hasLitter = Number.isInteger(score);
				let integer = Math.floor(score);
				for(let i=0;i<integer;i++){
					result.push(STAR_ON);
				}
				if(!hasLitter){
					result.push(STAR_HALF);
				}
				while(result.length<LENGTH){
					result.push(STAR_OFF);
				}
				return result
			}

		}
	}
		
</script>
<style lang="less">
	.star{
		&.star-48{
			.star-item{
				display:inline-block;
				width:0.48rem;
				height:0.48rem;
				margin-right:0.44rem;
				&.on{
					background:url(../../../static/imgs/star_on.png);
					background-size:cover;
				}
				&.half{
					background:url(../../../static/imgs/star_half.png);
					background-size:cover;
				}
				&.off{
					background:url(../../../static/imgs/star_off.png);
					background-size:cover;
				}
			}
		}

		&.star-36{
			.star-item{
				display:inline-block;
				width:0.36rem;
				height:0.36rem;
				margin-right:0.1rem;
				&.on{
					background:url(../../../static/imgs/star_on.png);
					background-size:cover;
				}
				&.half{
					background:url(../../../static/imgs/star_half.png);
					background-size:cover;
				}
				&.off{
					background:url(../../../static/imgs/star_off.png);
					background-size:cover;
				}
			}
		}
		&.star-24{
			.star-item{
				display:inline-block;
				width:0.24rem;
				height:0.24rem;
				margin-right:0.05rem;
				&.on{
					background:url(../../../static/imgs/star_on.png);
					background-size:cover;
				}
				&.half{
					background:url(../../../static/imgs/star_half.png);
					background-size:cover;
				}
				&.off{
					background:url(../../../static/imgs/star_off.png);
					background-size:cover;
				}
			}
		}
	}
	
</style>


