<template>
	<div class="seller" ref="sellerWrap">

		<div>
			<div class="shopInfo">
				<h1 class="name">{{seller.name}}</h1>
				<div class="desc">
					<star class="star" :size="36" :score="seller.score"></star>
					<span class="ratingCount">({{seller.ratingCount}})</span>
					<span class="sellCount">月售{{seller.sellCount}}单</span>
				</div>
				<div class="collect" @click="collectflag=!collectflag">
            		<i class="icon iconfont icon-xin" :class="{'active':collectflag}"></i>
            		<div class="text">{{collectflag?'已收藏':'收藏'}}</div>
          		</div>
				<ul class="delivery">
					<li class="price">
						<h1>起送价</h1>
						<div class="content">
							<span>{{seller.minPrice}}</span>元
						</div>
						
					</li>

					<li class="price">
						<h1>商家配送</h1>
						<div class="content">
							<span>{{seller.deliveryPrice}}</span>元
						</div>
						
					</li>

					<li class="price">
						<h1>平均配送时间</h1>
						<div class="content">
							<span>{{seller.deliveryTime}}</span>分钟
						</div>
						
					</li>
				</ul>				
			</div>
			<div class="line"></div>
			<div class="activity">
				<h1 class="title">公告与活动</h1>
				<div>
					<p class="bulletin">{{seller.bulletin}}</p>
				</div>
				<ul v-if="seller.supports" class="supports">
					<li class="supports-item" v-for="item in seller.supports">
						<span class="icon" :class="iconClass[item.type]"></span>
						<span class="text">{{item.description}}</span>
					</li>
				</ul>
			</div>
			<div class="line"></div>
			<div class="view">
				<h1>商家实景</h1>
				<div ref="picWarp">
					<div class="image">
						<img v-for="pic in seller.pics" :src="pic" alt="">
					</div>
				</div>
				
			</div>
			<div class="line"></div>
			<div class="info">
				<h1>商家信息</h1>
				<ul >
					<li v-for="info in seller.infos" class="info-item">
						{{info}}
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>
<script>
	import {mapGetters} from 'vuex'
	import star from '../star/star'
	export default{
		data(){
			return{
				collectflag: false
			}
			
		},
		computed:{
			...mapGetters([
				'seller'
				])
		},
		created(){
			this.$store.dispatch('getSeller');
			this.iconClass = ['decrease','discount','special','invoice','guarantee'];
			this.$nextTick(()=>{
				setTimeout(()=>{
					this._initseller();
					this._initpic();
					},100);
			
				
			})
		},
		methods:{
			_initseller(){
				this.sellerwrap = new BScroll(this.$refs.sellerWrap,{
					click:true
				});
			},
			_initpic(){
				this.picwarp = new BScroll(this.$refs.picWarp,{
					scrollX:true,
					eventPassthrough:'vertical',
					click:true

				});
			}
		},
		components:{
			star
		}
	}
</script>
<style lang="less">
	.seller{
		position: absolute;
        top: 3.48rem;
        bottom: 0;
        left: 0;
        width: 100%;
        overflow:hidden;
        .shopInfo{
		padding:0.36rem;
		border-bottom:1px solid rgba(7,17,27,0.1);
		.name{
			font-size:0.28rem;
			color:rgb(1,17,27);
			line-height:0.28rem;
			font-weight:600;
			margin-bottom:0.16rem;
		}
		.desc{
			border-bottom:1px solid rgba(7,17,27,0.1);
			padding-bottom:0.36rem;
			.star{
				display:inline-block;
				vertical-align:middle;

			}
			.ratingCount{
				display:inline-block;
				margin-right:0.24rem;
				font-size:0.2rem;
				line-height:0.36rem;
				color:rgb(77,85,93);
				vertical-align:middle;

			}
			.sellCount{
				display:inline-block;
				font-size:0.2rem;
				line-height:0.36rem;
				color:rgb(77,85,93);
				vertical-align:middle;
			}

		}
		.collect{
			position: absolute;
			right:0.16rem;
			top:0.4rem;
			width:1rem;
			text-align:center;
			.icon-xin{
				font-size:0.48rem;
				color:#d4d6d9;
				line-height:0.48rem;
				&.active{
					color:rgb(240,20,20);
				}
			}
			.text{
				font-size: 0.2rem;
				line-height: 0.28rem;
			}
		}
		.delivery{
			display:flex;
			padding-top:0.36rem;
			.price{
				flex:1;
				text-align:center;
				border-right:1px solid rgba(7,17,27,0.1);

				&:last-child{
					border-right: none;
				}
				h1{
					font-size:0.2rem;
					line-height:0.2rem;
					color:rgb(147,153,159);
					margin-bottom: 0.08rem;
				}
				.content{
					font-size:0.2rem;
					span{
						font-size:0.48rem;
						font-weight: 200;
						line-height: 0.48rem;
					}
				}
				
			}
		}
		
	}
	.line{
		width:100%;
		height: 0.32rem;
		background: #f3f5f7;
		border-bottom: 1px solid rgba(7,17,27,0.1);
	}
	.activity{
		padding:0.36rem 0.36rem 0 0.36rem;
		border-bottom: 1px solid rgba(7,17,27,0.1);
		.title{
			margin-bottom: 0.16rem;
            line-height: 0.28rem;
            color: rgb(7,17,27);
            font-size: 0.28rem;
            font-weight:700;
		}
		.bulletin{
			 padding: 0 0.24rem 0.32rem 0.24rem;
			 font-size:0.24rem;
			 font-weight:200;
			 color:rgb(240,20,20);
			 line-height: 0.48rem;
		}
		.supports{

			.supports-item{
				 padding: 0.32rem 0.24rem;
                 border-bottom: 1px solid rgba(7,17,27,0.1);
                 &:first-child{
                 	border-top: 1px solid rgba(7,17,27,0.1);
                 }
                 &:last-child{
                 	border-bottom: none;
                 }
				.text{
					vertical-align:middle;
					font-size:0.24rem;
					font-weight:200;
					line-height:0.24rem;

				}
				.icon{
					display:inline-block;
					width:0.32rem;
					height:0.32rem;
					
      				background-repeat:no-repeat;
      				vertical-align:middle;

					&.decrease{
						background:url(../../../static/imgs/decrease.png);
						background-size:cover;
						
					}
					&.discount{
						background:url(../../../static/imgs/discount.png);
						background-size:cover;
						
					}
					&.special{
						background:url(../../../static/imgs/special.png);
						background-size:cover;
						
					}
					&.invoice{
						background:url(../../../static/imgs/invoice.png);
						background-size:cover;
						
					}
					&.guarantee{
						background:url(../../../static/imgs/guarantee.png);
						background-size:cover;
						
					}

				}
			}

		}

	}
	.view{
		padding:0.36rem;
		h1{
			margin-bottom: 0.24rem;
            line-height: 0.28rem;
            color: rgb(7,17,27);
            font-size: 0.28rem;
            font-weight:700;
		}
		.image{
			width:10.1rem;
			overflow:hidden;
			white-space: nowrap;
			img{
				width: 2.4rem;
				height: 1.8rem;
				margin-right:0.12rem;
			}

			
		}


	}
	.info{
		padding:0.36rem;
		h1{
			margin-bottom: 0.24rem;
            line-height: 0.28rem;
            color: rgb(7,17,27);
            font-size: 0.28rem;
            font-weight:700;
		}
		.info-item{
			font-size:0.24rem;
			font-weight: 200;
			color:rgb(7,17,27);
			line-height:0.32rem;
			padding: 0.32rem 0.24rem;
			border-bottom:1px solid rgba(7,17,27,0.1);
			&:first-child{
				border-top: 1px solid rgba(7,17,27,0.1);
			}
		}
	}
}
	
</style>


