<template>
	<transition enter-active-class="animated fadeInLeft" leave-active-class="animated fadeOut">
		<div class="fooddetail" v-show="foodshow" ref="foodWarp">
	<div>
		<div class="detail-content">
			<div class="image-header">
				<img :src="food.image" alt="">
			</div>
			<div class="leftIcon" @click ='hidefood'>
				<i class="el-icon-arrow-left"></i>
			</div>
			<div class="info">
				<div class="name">{{food.name}}</div>
				<div class="desc">
					<span>月售{{food.sellCount}}</span>
					<span>好评率{{food.rating}}%</span>
				</div>
				<div class="price">
					<span class="unit">￥</span>{{food.price}}
					<span class="oldPrice" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
				</div>
					<div class="cartcontrol-warp">
					<cartControl :food = 'food'></cartControl>
				</div>				
				<div class="shopCart">
					<div class="text" @click="addCart" v-show="!food.num">加入购物车</div>
				</div>			
			</div>
		</div>
		<div class="line"></div>
		<div class="shop">
			<div class="title">商品介绍</div>
			<div class="foodInfo">{{food.info}}</div>
		</div>
		<div class="line"></div>
		<ratingselect></ratingselect>
		<div class="evaluation">
		</div>
		<div class="ratings-warp">
			<ul v-show ='food.ratings && food.ratings.length'>
				<li v-for="rating in selectRatings" class="rating-item">
					<div class="user">
						<div class="time">{{rating.rateTime | getTimes}}</div>
						<div>
							<span class="name">{{rating.username}}</span>
							<img :src="rating.avatar" alt="">
						</div>
							
					</div>
					
					<div class="comment">
						<span>
							<i v-show="rating.rateType ===0" class="icon iconfont icon-damuzhion"></i>
							<i v-show = 'rating.rateType === 1' class="icon iconfont icon-down"></i>
						</span>
						<span class="text">{{rating.text}}</span>
					</div>
					
				</li>
			</ul>
		</div>
	</div>
		
	</div>
	</transition>	
	
</template>

<script>
	import {mapGetters} from 'vuex'
	import cartControl from '../cartControl/cartControl.vue'
	import ratingselect from '../ratingSelect/ratingSelect.vue'
	export default{
		computed:{
			...mapGetters([
				'foodshow',
				'food',
				'selectRatings'
				])
		},
		watch:{
			food(){
			this.$store.dispatch('resetType',['推荐','吐槽','全部']);
			this.$nextTick(()=>{
				setTimeout(()=>{
					if(!this.foodwarp){
						this.foodwarp = new BScroll(this.$refs.foodWarp,{
							click:true
							})
						}else{
							this.foodwarp.refresh();
							}
					
					},100);
				
				});
			}
		},
		
		filters:{
			getTimes(date){
				var time = new Date(date);
				var y = time.getFullYear();
				var m = time.getMonth() + 1;
				var d = time.getDay();
				var h = time.getHours();
				var min = time.getMinutes();
				var s = time.getSeconds();
				return (y+"-"+m+"-"+d+" "+h+":"+min);

			}
		},
		methods:{
			hidefood(){
				this.$store.dispatch('hidefood');
			},
			// _initfood(){
			// 	this.foodwarp = new BScroll(this.$refs.foodWarp,{
			// 		click:true
			// 	});
			// },
			addCart(){
				this.$set(this.food,'num',1);

			}
		},
		components:{
			cartControl,
			ratingselect
		}
	}
</script>

<style lang="less">
	.fooddetail{
		background-color: white;
		position:fixed;
		top: 0;
		z-index: 999;
		left: 0;
		right: 0;
		bottom: 0;
		.detail-content{
			border-bottom:1px solid rgba(7,17,27,0.1);
			.image-header{
				position: relative;
				width: 100%;
				padding-bottom: 100%;
				img{
					position: absolute;
					width: 100%;
					height: 100%;
					left: 0;
					top:0;
				}
			}
			.leftIcon{
				font-size:0.4rem;
				position: fixed;
				top:0.2rem;
				left:0.1rem;
				color:white;
			}
		}
		.info{
			position: relative;
			padding:0.36rem;
			.name{
				font-size:0.28rem;
				line-height:0.28rem;
				font-weight:700;
				color:rgb(7,17,27);
				margin-bottom:0.16rem;
			}
			.desc{
				margin-bottom:0.36rem;
				span{
					font-size:0.2rem;
					line-height:0.2rem;
					color:rgb(147,153,159);
					margin-right:0.24rem;
				}
			}
			.price{
				display:inline-block;
				font-size:0.28rem;
				font-weight:700;
				color:rgb(240,20,20);
				line-height:0.48rem;
				.unit{
					font-size:0.2rem;
					font-weight:normal;
					color:rgb(240,20,20);
					line-height:0.48rem;
				}
				
			}
			.shopCart{
				display:inline-block;
				position:absolute;
				right: 0.36rem;
				.text{
					box-sizing:border-box;
        			height:100%;
        			line-height:24px;
        			color:white;
        			font-size:10px;
        			padding:0 12px;
       	 			border-radius:12px;
        			background:rgb(0,160,220);
        			
				}

			}
			.cartcontrol-warp{
						position:absolute;
      					right:0.4rem;
      					bottom:0.4rem;
				}		
		}
		.line{
		width:100%;
		height: 0.32rem;
		background: #f3f5f7;
		border-bottom: 1px solid rgba(7,17,27,0.1);
		}
		.shop{
			padding:0.36rem;
			border-bottom: 1px solid rgba(7,17,27,0.1);
			.title{
				font-size:0.28rem;
				line-height:0.28rem;
				font-weight:500;
				color:rgb(7,17,27);
				margin-bottom:0.16rem;
				
			}
			.foodInfo{
				padding:0 0.16rem;
				font-size:0.24rem;
				font-weight:200;
				color:rgb(77,85,93);
				line-height:0.48rem;
			}
		}
		
		.ratings-warp{
			padding:0 0.36rem;
			.rating-item{
				padding: 0.32rem 0;
				border-bottom:1px solid rgba(7,17,27,0.1);
				.user{
					display: flex;
					justify-content:space-between;
					margin-bottom:0.12rem;
					.time{
						font-size: 0.2rem;
						color:rgb(147,153,159);
						line-height: 0.48rem;
					}
					.name{
						font-size:0.2rem;
						vertical-align:top;
					}		
					img{
						vertical-align:top;
						height: 0.24rem;
						widows: 0.24rem;
						border-radius: 50%;
					}						
				}
				.comment{
					.icon-damuzhion{
						font-size:0.24rem;
						color:rgb(0,160,220);

					}
					.icon-down{
						font-size:0.24rem;		
						color:rgb(147,153,159);
					}
					.text{
						font-size: 0.24rem;
						color:rgb(7,17,27);
						line-height: 0.32rem;
					}
				}

			}

		}
	}
</style>


