<template>
	<div class="ratingselect">
		
		<div class="rating-type">
			<span class="block all" :class="{'active':2===selectType }" @click="select(2)">{{types[2]}}<span class="count">{{ratings.length}}</span></span>
			<span class="block positive" :class="{'active':0===selectType }" @click="select(0)">{{types[0]}}<span class="count">{{positiveCount}}</span></span>
			<span class="block negative" :class="{'active': 1===selectType}" @click="select(1)">{{types[1]}}<span class="count">{{negativeCount}}</span></span>
		</div>
		<div class="induce" :class="{'on':onlyContent}" @click="toggle">
			<i class="icon iconfont icon-duihao"></i>
			<span class="title">只看有内容的评价</span>
		</div>
	</div>
</template>
<script>
	import {mapGetters} from 'vuex'
	export default{
		computed:{
			...mapGetters([
				'ratings',
				'types',
				'selectType',
				'onlyContent'

				]),
			positiveCount(){
				return this.ratings.filter( rating => {
					return rating.rateType === 0;
				}).length;
			},
			negativeCount(){
				return this.ratings.filter( rating => {
					return rating.rateType === 1;
				}).length;
			}
		},
		methods:{
			select(type){
				this.$store.dispatch('select',type);
			},
			toggle(){
				this.$store.dispatch('toggle');
			}
		}

	}
</script>
<style lang="less">
	.ratingselect{
		// border-bottom: 1px solid rgba(7,17,27,0.2);
		.rating-type{
			padding:0.36rem 0;
			margin: 0 0.36rem;
			border-bottom: 1px solid rgba(7,17,27,0.2);
			.block{
				display:inline-block;
				padding: 0.16rem 0.24rem;
				
				margin-right: 0.16rem;
				border-radius: 0.02rem;
				color:rgb(77,85,93);
				font-size: 0.24rem;
				&.active{
					color:#fff;
				}
				&.all,&.positive{
					background: rgba(0,160,220,0.2);
				}
				&.active{
					background: rgb(0,160,220,);
				}
				&.negative{
					background: rgba(77,85,93,0.2);
					&.active{
						background: rgb(77,85,93);
					}
				}
				.count{
					margin-left: 0.04rem;
					line-height: 0.1rem;
					font-size: 0.16rem;
				}
			}
			
		}
		.induce{
			padding:0.24rem 0.36rem;
			line-height: 0.48rem;
			border-bottom: 1px solid rgba(7,17,27,0.2);
			color:rgb(147,153,159);
			&.on{
				.icon-duihao{
					color:#00c850;
				}
			}
			.title{

				font-size: 0.28rem;
				color: rgb(147,153,159);
				line-height: 0.48rem;
				margin-left: 0.08rem;
			
			}
		}
	}
</style>


