<style lang="less">
	.tab{
		display:flex;
		height:0.8rem;
		line-height:0.8rem;
		font-size:0.28rem;
		width:100%;
		color:rgb(240,20,20);
		border-bottom:1px solid rgba(7,17,27,0.1);
		.tab-item{
